export default {
	data() {
		return {};
	},
	props: {
		loading: {
			type: Boolean,
			default: false
		},
		color: {
			type: String,
			default: '#ffffff'
		},
		bgcolor: {
			type: String,
			default: '#333333'
		},
		width: {
			type: String,
			default: '200rpx'
		},
		height: {
			type: String,
			default: '100rpx'
		},
		borderWidth: {
			type: String,
			default: '3rpx'
		},
		fontsize: {
			type: String,
			default: '25rpx'
		},
		isdisable: {
			type: Boolean,
			default: false
		}
	},
	computed: {
		loadingsize: function() {
			const str = this.height
			if (str.indexOf('rpx') != -1) {
				return str.slice(0, str.indexOf('rpx')) / 4 + 'rpx'
			} else if (str.indexOf('upx') != -1) {
				return str.slice(0, str.indexOf('upx')) / 4 + 'upx'
			} else {
				return '25rpx'
			}

		}
	}
};
